@import url(https://fonts.googleapis.com/css?family=Ubuntu);
.footer-container{
    display: flex;
    height: 20px;
    font-family: 'Ubuntu', sans-serif;
}
.testmonials{
    flex :1;
    color: #ffffff;
}
.getintouch{
  display: flex;
  flex: 1;
  flex-direction: column; /* 👉 τα βάζει κάθετα */
  align-items: stretch;   /* όλα ίδιου πλάτους */
  gap: 12px;              /* απόσταση μεταξύ πεδίων */
  color: #ffffff;
}
.getintouch button{
    color: #ffffff;
}
.getintouch input{
    color: #ffffff;
}
.msg{
    padding: 2em;
    
}
.name{
    padding: 0.5em;
}
.email{
    padding: 0.5em;
}
.getintouch textarea{
    color: #ffffff;
    
}
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: auto;
  }

  .getintouch {
    width: 100%;
  }

  .getintouch input,
  .getintouch textarea {
    width: 90%;
  }
}
