@import url(https://fonts.googleapis.com/css?family=Ubuntu);
.pj-container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;              /* ✅ επιτρέπει να “σπάνε” οι κάρτες σε νέες σειρές */
    gap: 24px;                    /* απόσταση μεταξύ project boxes */
    text-align: center;
    color: white;
    font-family: 'Ubuntu', sans-serif;
}
.pj-card {
  flex: 1 1 300px;              /* responsive πλάτος – τουλάχιστον 300px */
  max-width: 350px;
  background: #2a2a2a;
  border-radius: 10px;
  padding: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s ease;
}

.pj-card:hover {
  transform: translateY(-5px);
}

.pj-thumb {
  width: 100%;
  height: auto;
  border-radius: 8px;
  display: block;
}

.pj-card h3 {
  margin: 12px 0 6px;
  font-size: 20px;
}

.pj-card p {
  font-size: 14px;
  opacity: 0.85;
}

/* 📱 Responsive Breakpoints */

/* Tablet */
@media (max-width: 992px) {
  .pj-container {
    justify-content: center;
    gap: 20px;
  }

  .pj-card {
    flex: 1 1 45%;              /* δύο projects ανά σειρά */
  }
}

/* Κινητό */
@media (max-width: 600px) {
  .pj-container {
    flex-direction: column;
    align-items: center;
    padding: 16px;
  }

  .pj-card {
    flex: 1 1 100%;
    max-width: 90%;
  }

  .pj-card h3 {
    font-size: 18px;
  }
}
