@import url(https://fonts.googleapis.com/css?family=Ubuntu);

.header-container{
    display: flex;
}
.img{
    flex: 1;
   
    
}
.header{
    flex: 1;
    color: white;
    font-family: 'Ubuntu', sans-serif;
}
.header h1{
    font-size: 50px ;
    font-weight: 700;
}
@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    text-align: center;
  }

  .header h1 {
    font-size: 32px;
  }

  img {
    width: 100%;
    height: auto;
  }
}