@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
@import url(https://fonts.googleapis.com/css?family=MonteCarlo);

.container{
    display: flex;
    padding: 20px 10%;
    
    
}
.brand{
    flex:1;
    font-size: 40px;
    font-weight: 900;
    color: white;
    font-family: 'MonteCarlo', cursive;
    

}
.menu{
  flex: 1;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}
a{
    text-decoration: none;
    color: white;
    font-size: large;
    margin-right: 20px;
    font-family: 'Roboto Mono', monospace;

}
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
  }

  .brand {
    font-size: 30px;
    margin-bottom: 10px;
  }

  .menu {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  a {
    font-size: 16px;
  }
}
